<template>
    <section class="bg-white text-white">
         <figure>
            <img src="@/assets/img/singlepole-top-blue.png" alt="" />
         </figure>

         <div class="bg-blue-180 pb-12">
            <h2 class="text-3xl py-6 text-white 
            md:text-6xl
            lg:text-4xl">- Olvide mi contraseña -</h2>            

            <div class="container border-dashed border-2 rounded border-white p-5">
              <form class="flex flex-col gap-x-3 gap-y-6 flex-wrap shadow-round bg-darkBlue text-xl px-10 py-8">        

                <div v-show="!client_exists">
                  <h3 class="w-full text-left border-b font-bold text-2xl pb-2">Verificación de usuario</h3>
                  <div class="flex flex-wrap items-center gap-4 pt-6" >                  
                    <input name="email" 
                      type="email" 
                      placeholder="E-Mail *" 
                      required
                      v-model="email" 
                      class="w-2/5 border rounded-4xl text-orange bg-darkBlue text-left px-6 py-2">
                    <button class="bg-red rounded-4xl text-white px-6 py-2" type="button" >Enviar</button>
                    <small class="w-full text-left">Teclee su email para verificar que el usuario es correcto, le llegará una notificación al mismo para cambiar su contraseña.</small>
                  </div>
                </div>

<!-- Seguridad -->
                <div v-show="client_exists">
                  <h3 class="w-full text-left border-b font-bold text-2xl pb-2">Teclee su nueva contraseña</h3>
                  <div class="flex flex-wrap gap-x-3 w-full text-left font-bold text-2xl pt-6" >
                    <input name="password" 
                      :type="(showpass) ? 'text' : 'password'" 
                      placeholder="Contraseña *"
                      required
                      v-model="password" 
                      class="w-3/10 border rounded-4xl text-orange bg-darkBlue text-left px-6 py-2">    

                    <input name="repassword" 
                      :type="(showpass) ? 'text' : 'password'" 
                      placeholder="Repetir Contraseña *"
                      required
                      v-model="repassword"                     
                      class="w-3/10 border rounded-4xl text-orange bg-darkBlue text-left px-6 py-2">                      
                    <div class="flex items-center gap-x-4">                    
                      <button type="button" @click="showpass=!showpass" v-show="!showpass"><i class="fas fa-eye-slash"></i></button>
                      <button type="button" @click="showpass=!showpass" v-show="showpass"><i class="fas fa-eye"></i></button>
                      <small class="text-red-700" v-show="!samepass">Las contraseñas deben ser iguales.</small>
                    </div>
                  </div>
                </div>

                <div class="flex flex-col text-left w-full py-6">
                  <div v-if="error_message" class="flex bg-red-500 rounded-3xl w-full text-left text-4xl p-4 my-3">
                    <i class="fa fa-times-circle"></i>
                    <h5 class="text-xl self-center px-2">
                      Error enviando informacion. No se pudo actualizar la contraseña
                      <p>{{error_message_text}}</p>
                    </h5>
                  </div>
                  <div v-if="success_message" class="flex bg-green-500 rounded-3xl w-full text-left text-4xl p-4 my-3">
                    <i class="fa fa-check-circle"></i>
                    <h5 class="text-xl self-center px-2">La información ha sido enviada satisfactoriamente.</h5>
                  </div>
                </div>

                <button class="uppercase bg-orange text-white text-xl rounded-4xl font-bold ml-auto px-12 py-1"
                :class="{'cursor-not-allowed btn-disable': !client_exists}"
                @click="updPass">Actualizar</button>
              </form>
            </div>
         </div>
         
    </section>
</template>
<script>
import Authorization from "@/services/user/auth.service";

export default {
    name:'ForgotPassword',
    data() {
      return {
        client_exists: false,
        error_message_text: '',
        error_message: false,
        success_message: false,
        activeTerm: false,    
        samepass: true,            
        showpass: false,                    
        email: '',        
        password: '',
        repassword: ''        
      }
    },      
    created() {
      // if tojen param exist is a mail confirmation
      const token = this.$route.params.token
      if (token) {
        // const user = Authorization.decodeToken(decodeToken)
        this.client_exists = true
      }
    }, 
    watch: {
      repassword() {
        this.samepass =  (this.repassword == this.password)
        this.samepass = (this.repassword == '' && this.password == '')
      }      
    },
    methods: {      
      comparePass() {
        this.samepass = (this.password != this.repassword)
      },
      updPass(e) {
        e.preventDefault();
        if(!client_exists) {
          console.log('envio')
        }
      }
    }
}
</script>
<style scoped>
  .ui.dropdown {
    width: 33% !important;
  }   
</style>